/*--------------------------------------------------------------
1. GENERAL
---------------------------------------------------------------*/
body, html {
     -moz-osx-font-smoothing: grayscale;
}
body {
    background-color: #fff;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color:#6f6f6f;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
    font-smoothing: antialiased;
}
a {
    color: #EFBB20;
    outline: 0 none;
    text-decoration: none;
}
a:hover,
a:focus {
    text-decoration: none;
    outline: 0 none;
}
a:active,
a:hover {
    color: #333333;
}
p a {
    color: #333333;
}
img {
    border-style: none;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
}
hr {
    clear: both;
}
section,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
summary {
    display: block;
}
address {
    font-style: italic;
}
table {
    background-color: transparent;
    width: 100%;
}
table thead th {
    color: #333;
}
table td {
    padding: 15px 10px;
}
p,
address,
pre,
hr,
ul,
ol,
dl,
dd,
table {
    margin-bottom: 24px;
}
 ::selection {
     color: #000;
     background:#dbdbdb;
}
 ::-moz-selection {
     color:#000;
     background:#dbdbdb;
}
.overflow-hide {
    overflow: hidden;
}
.overflow-auto {
    overflow: auto;
}
.clear {
    clear: both;
}
.pull-none {
    float: none;
}
::selection {
    background: #3396d1;
    color: #fff;
}
.op{
	font-family: 'Open Sans', sans-serif;
}
.dis-block{
	display:block;
	width: 100%;
}
