.top-bar li{
	position:relative;
	padding:0 15px;
}
.top-bar li:first-child{
	padding-left:0;
}
.top-bar li:last-child{
	padding-right:0;
}
.top-bar li:after{
	content: "";
	position: absolute;
	height: 80%;
	opacity: 0.5;
	width: 1px;
	background: #fff;
	transform: rotate(15deg);
	right: 0;
	top: 15%;
}
.top-bar li:last-child:after{
	content:none;
}
.dez-topbar-left {
    float: left;
}
.dez-topbar-right {
    float: right;
}
.dez-topbar-center,
.dez-topbar-left,
.dez-topbar-right {
    padding-left: 15px;
    padding-right: 15px;
}
.dez-topbar-left ul,
.dez-topbar-right ul{
	list-style:none;
	margin:0;
	padding:0;
}
.dez-topbar-left ul li,
.dez-topbar-right ul li{
	display:inline-block;
}
.dez-topbar-left ul li{
	padding-right:15px;
}
.dez-topbar-left ul li a,
.dez-topbar-right ul li a{
	color:#fff;
	
}
.topbar-social li a{
