/*---------------------------------------------------------------
16. LOGO ( company identify  symbol )
---------------------------------------------------------------*/
/*=== >logo for header ===*/

.logo-header {
    display: table;
    float: left;
    vertical-align: middle;
    padding: 0;
    color: #EFBB20;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 0;
    margin-right: 0;
    width: 140px;
    height: 80px;
    position: relative;
    z-index: 9;
	transition:all 0.5s;
	-moz-transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-o-transition:all 0.5s;
}
.logo-footer {
    display: table;
    vertical-align: middle;
    padding: 0;
    color: #EFBB20;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 0;
    margin-right: 0;
    max-width: 100%;
    height: 60px;
}
.logo-header > a,
.logo-footer > a{
    display: table-cell;
    vertical-align: middle;
}
.logo-header.center-block {
    margin-left: auto;
    margin-right: auto;
    float: none;
}
.logo-header img,
.logo-footer img {
    height: auto;
	width: auto;
	max-width: 180px;
}
.logo-header span,
.logo-footer span {
    font-size: 20px;
    color: #EFBB20;
    letter-spacing: 20px;
}
/*= When header is sticky =*/

.is-fixed .logo-header,
.is-fixed.header-curve .logo-header {
    height: 70px;
    width: 140px;
	transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-moz-transition:all 0.5s;
	-o-transition:all 0.5s;
}
/*= Set width for mobile  =*/

@media only screen and (max-width: 767px) {
    .logo-header,
    .is-fixed.header-curve .logo-header,
	.is-fixed .logo-header,
	.is-fixed.header-curve .logo-header{
        width: 100px;
		max-width: 100px;
		height:50px;
    }
	.logo-header img, 
	.logo-footer img {
		max-width: 150px;
	}
    .is-fixed .logo-header a img,
    .is-fixed.header-curve .logo-header a img {
        vertical-align: sub;
    }
	.site-header .navbar-toggler,
	.site-header .is-fixed .navbar-toggler{
		 margin: 18px 0 14px 10px;
	}
	.site-header .extra-nav,
	.site-header .is-fixed .extra-nav{
		padding: 11px 0;
	}
}
/*---------------------------------------------------------------
17. MAIN BAR ( place logo & main-navigation )
---------------------------------------------------------------*/

.main-bar {
    background: #FFFFFF;
    width: 100%;
    position: relative;
}
.header-curve .logo-header:before,
.header-curve .logo-header:after {
    background-color: #fff;
    content: "";
    position: absolute;
    bottom: 0;
    height: 100%;
    z-index: -1;
}
.header-curve .logo-header:before {
    right: -50px;
    width: 80%;
    transform: skew(30deg);
}
.header-curve .logo-header:after {
    right: 10%;
    width: 2000px;
}
.is-fixed.header-curve .logo-header img {
    margin-top: -0;
}
@media only screen and (max-width: 767px) {
    .header-curve .logo-header:after {
        right: 0;
        left: auto;
    }
}
/*---------------------------------------------------------------
18. NAVIGATIONS
	>mena-menu
	>mobile-menu
	>footer-menu
---------------------------------------------------------------*/
/*== >mena-menu ==*/

.navbar-toggler {
    border: 0 solid #efbb20;
    font-size: 16px;
    line-height: 24px;
    margin: 32px 0 30px 10px;
    padding: 0;
}
.navbar-toggler span {
    background: #666;
}
.is-fixed .navbar-toggler {
    margin:28px 0 20px 10px;
}
.header-nav {
    position: relative;
    padding: 0;
}
.header-nav .nav {
    float: right;
}
.header-nav .nav i {
    font-size: 9px;
    margin-left: 3px;
    margin-top: -3px;
    vertical-align: middle;
}
.header-nav .nav > li {
    margin: 0px;
    font-weight: 400;
    text-transform: uppercase;
    position: relative;
}
.header-nav .nav > li > a {
    border-radius: 0px;
    color: #000;
    font-size: 14px;
    padding: 30px 12px;
    cursor: pointer;
	font-weight: 600;
	display: inline-block;
}
.header-nav .nav > li > a:hover {
    background-color: transparent;
    color: #efbb20;
}
.header-nav .nav > li > a:active,
.header-nav .nav > li > a:focus {
    background-color: transparent;
}
.header-nav .nav > li.active > a,
.header-nav .nav > li.current-menu-item > a {
    background-color: transparent;
    color: #EFBB20;
}
.header-nav .nav > li:hover > a {
    color: #EFBB20;
}
.header-nav .nav > li:hover > .sub-menu,
.header-nav .nav > li:hover > .mega-menu {
    opacity: 1;
    visibility: visible;
    margin-top: 0;
    transition: all 0.5s ease;
}
.header-nav .nav > li > .sub-menu,
.header-nav .nav > li > .mega-menu {
	box-shadow:0 0 40px rgba(0, 0, 0, 0.2);
}
.header-nav .nav > li .sub-menu {
    background-color: #ffffff;
    border: 1px solid #f4f4f4;
    display: block;
    left: 0;
    list-style: none;
    opacity: 0;
    padding: 10px 0;
    position: absolute;
    visibility: hidden;
    width: 220px;
    z-index: 10;
	border-radius: 4px;
}
.header-nav .nav > li .sub-menu li {
    border-bottom: 0px solid #f4f4f4;
    position: relative;
}
.header-nav .nav > li .sub-menu li a {
    color: #505050;
    display: block;
    font-size: 14px;
    padding: 8px 20px;
    text-transform: capitalize;
    transition: all 0.15s linear;
    -webkit-transition: all 0.15s linear;
    -moz-transition: all 0.15s linear;
    -o-transition: all 0.15s linear;
	font-weight: 500;
	font-family: "Montserrat",sans-serif;
}
.header-nav .nav > li .sub-menu li a:hover {
    background-color: #F2F2F2;
    color: #EFBB20;
    text-decoration: none;
}
.header-nav .nav > li .sub-menu li:hover > a {
    color: #EFBB20;
}
.header-nav .nav > li .sub-menu li:last-child {
    border-bottom: 0px;
}
.header-nav .nav > li .sub-menu.left{
    left: auto;
    right: 0;
}
.header-nav .nav > li .sub-menu li .fa {
    color: inherit;
    display: block;
    float: right;
    font-size: 15px;
    position: absolute;
    right: 15px;
    top: 12px;
	opacity: 1;
}
.header-nav .nav > li .sub-menu li .fa.fa-nav {
	color: inherit;
    display: inline-block;
    float: none;
    font-size: 13px;
    margin-right: 5px;
    opacity: 1;
    position: unset;
    right: 10px;
    top: 12px;
}
.header-nav .nav > li .sub-menu li > .sub-menu {
    left: 220px;
    transition: all 0.5s ease;
}
.header-nav .nav > li .sub-menu li:hover > .sub-menu {
    left: 220px;
    margin: 0px;
    opacity: 1;
    top: -1px;
    visibility: visible;
}
.header-nav .nav > li .sub-menu li > .sub-menu.left, 
.header-nav .nav > li .sub-menu li:hover .sub-menu.left {
    right: auto;
    left: 219px;
}
.header-nav .nav > li .sub-menu li:hover > .sub-menu:before {
    background-color: transparent;
    bottom: 0px;
    content: '';
    display: block;
    height: 100%;
    left: -6px;
    position: absolute;
    top: 0px;
    width: 6px;
}
.header-nav .nav > li.has-mega-menu {
    position: inherit;
}
.header-nav .nav > li .mega-menu {
    background-color: #ffffff;
    border: 1px solid #f8f8f8;
    display: table;
    left: 0px;
    list-style: none;
    opacity: 0;
    position: absolute;
    right: 0px;
    visibility: hidden;
    width: 100%;
	font-family: Montserrat;
    margin-top: 20px;
	z-index: 9;
}
.header-nav .nav > li .mega-menu > li {
    display: table-cell;
    padding: 30px 0 25px;
    position: relative;
    vertical-align: top;
    width: 25%;
}
.header-nav .nav > li .mega-menu > li:after {
    content: "";
    background-color: rgba(0, 0, 0, 0.02);
    position: absolute;
    right: 0px;
    top: 0px;
    display: block;
    width: 1px;
    height: 100%;
}
.header-nav .nav > li .mega-menu > li:last-child:after {
    display: none;
}
.header-nav .nav > li .mega-menu > li > a {
    color: #000;
    display: block;
    font-size: 14px;
    padding: 0 20px;
	font-size: 13px;
    font-weight: 600;
}
.header-nav .nav > li .mega-menu > li ul {
    list-style: none;
    margin: 10px 0px 0px 0px;
    padding: 0px;
    width: 100%;
}
.header-nav .nav > li .mega-menu > li ul a {
    color: #505050;
    display: block;
    font-size: 13px;
    line-height: 34px;
    text-transform: capitalize;
    padding: 0 20px;
	font-weight: 500;
}
.header-nav .nav > li .mega-menu > li ul a:hover {
    color: #EFBB20;
}
.header-nav .nav .mega-menu a i {
    font-size: 14px;
    margin-right: 5px;
    text-align: center;
    width: 15px;
}
.header-nav .nav > li .sub-menu,
.header-nav .nav > li .mega-menu {
	border-top:0 solid #000;
}

.header-nav .nav > li.menu-item-has-children:before {
    content: "\f078";
    display: block;
    font-family: "FontAwesome";
    right: 4px;
    position: absolute;
    top: 50%;
    color: #999;
    margin-top: -8px;
    font-size: 8px;
}
