/* Skew */
.button-skew {
    position: relative;
    text-align: center;
    margin-right: 21px;
    transform: skewX(-20deg);
}
.button-skew span {
    display: inline-block;
    transform: skewX(20deg);
}
.button-skew i {
    background-color: inherit;
    position: absolute;
    height: 100%;
    margin-right: -32px;
    right: 0;
    top: 0;
    padding: 12px 12px;
    line-height: normal;
}
.button-skew i:before {
    display: inline-block;
    transform: skewX(20deg);
}
.button-skew.button-sm i {
    padding: 7px 10px;
    margin-right: -27px;
}
.button-skew.button-lg i {
    padding: 25px 15px;
    margin-right: -40px;
}
.button-skew.button-xl i {
    padding: 35px 22px;
    margin-right: -55px;
}
