/*== >Testimonials ======*/

.testimonial-pic {
    background: #FFF;
    width: 100px;
    height: 100px;
    position: relative;
    display: inline-block;
    border: 5px solid #FFF;
}
.testimonial-pic.radius {
    border-radius: 100%;
    -webkit-border-radius: 100%;
}
.testimonial-pic.radius img {
    width: 100%;
    height: 100;
    border-radius: 100%;
    -webkit-border-radius: 100%;
}
.testimonial-pic.shadow {
    box-shadow: 2px 3px 6px -3px rgba(0, 0, 0, 0.35);
}
.testimonial-pic.style1{
	border: 0 none;
    height: 100px;
    width: 100px;
}
.quote-left,
.quote-right {
    position: relative;
}
.quote-left:before,
.quote-right:after {
    font-family: "FontAwesome";
    position: absolute;
    z-index: 10;
    top: 15px;
}
.quote-left:before {
    content: "\f10d";
    left: 0;
}
.quote-right:after {
    content: "\f10e";
    left: auto;
    right: 0;
}
.testimonial-text {
    padding: 15px;
    position: relative;
	font-size:15px;
	font-family: "Montserrat",sans-serif;
	font-weight: 400;
}
.testimonial-detail {
    padding: 5px;
}
.testimonial-name{
    font-family: montserrat;
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
}
.testimonial-position {
	font-family: montserrat;
    font-size: 12px;
    font-style: inherit;
    text-transform: uppercase;
}
.testimonial-name,
.testimonial-position {
    display: block;
}
.testimonial-text p:last-child {
    margin: 0;
}
