.candidates-bx{
	position:relative;
}
.candidates-bx:before,
.candidates-bx:after{
	content:"\f10d";
	font-family:FontAwesome;
	position:absolute;
	color:#eaeefe;
	font-size:40px;
	top: 80px;
}
.candidates-bx:after{
	content:"\f10e";
	top:auto;
	bottom:-10px;
	right:0;
}

/* Quote Box */
.quote-bx{
	background-image:url(/static/media/pic4.ba3e9eca.jpg);
	background-size:cover;
	position:relative;
	border-radius:4px;
	box-shadow: 0 0 10px 0 rgba(0,24,128,0.1);
	padding: 30px 20px;
	z-index: 1;
	color:#fff;
}
.quote-bx:after{
	content:"";
	width:100%;
	height:100%;
	background-color:#000930;
	opacity:0.8;
	position:absolute;
	top:0;
	left:0;
	z-index:-1;
	border-radius:4px;
}
.quote-bx h4{
	color:#fff;
}
.quote-bx p{
	opacity:0.9;
}
.quote-info{
	z-index:2px;
	position:relative;
}

/* City Box */
.city-bx{
	height:180px;
	border-radius:4px;
	background-size:cover;
	z-index: 1;
	position:relative;
	box-shadow: 0 0 10px 0 rgba(0,24,128,0.1);
	transition:all 0.5s;
	-moz-transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-ms-transition:all 0.5s;
	-o-transition:all 0.5s;
}
.city-bx:after{
	content:"";
	width:100%;
	height:100%;
	background-color:#000930;
	opacity:0.5;
	position:absolute;
	top:0;
	left:0;
	z-index:-1;
	border-radius:4px;
	transition:all 0.5s;
	-moz-transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-ms-transition:all 0.5s;
	-o-transition:all 0.5s;
}
.city-bx:hover:after{
	background-color:#2e55fa;
	opacity:0.95;
}
.city-info{
	position:relative;
	z-index:1;
	color:#fff;
	padding: 20px 25px;
}
.city-info h5{
	font-size:22px;
	color:#fff;
	margin-bottom:5px;
}
.city-info span{
	color:#fff;
	opacity:0.9;
}
.pagination-bx .previous a{
